@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff") format("woff2");
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

.main-header {
  background-color: #81b3d2;
  position: relative;
}
@media (min-width: 768px) {
  .main-header .main-header {
    background-color: #ffffff;
  }
}

.main-header__logo {
  display: block;
  text-decoration: none;
  height: 96px;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  min-width: 320px;
  background-color: #E7E5E5;
}

html {
  height: 100%;
}

.banner__wrapper {
  background-image: url(../img/back-mobile.jpg);
  width: 100%;
  height: 389px;
  background-repeat: no-repeat;
  padding-top: 52px;
  background-size: cover;
  margin: 0;
  position: relative;
}
@media (min-resolution: 2dppx) {
  .banner__wrapper {
    background-image: url(../img/back-mobile@2x.jpg);
  }
}
@media (min-width: 768px) {
  .banner__wrapper {
    background-image: url(../img/tablet.jpg);
    height: 518px;
    background-repeat: no-repeat;
    width: 100%;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .banner__wrapper {
    background-image: url(../img/tablet@2x.jpg);
  }
}
@media (min-width: 1200px) {
  .banner__wrapper {
    background-image: url(../img/desktop.jpg);
    height: 518px;
    background-repeat: no-repeat;
    width: 100%;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .banner__wrapper {
    background-image: url(../img/desktop@2x.jpg);
  }
}

.banner__wrapper--catalog {
  height: 158px;
}
@media (min-width: 768px) {
  .banner__wrapper--catalog {
    height: 290px;
  }
}
@media (min-width: 1200px) {
  .banner__wrapper--catalog {
    height: 301px;
  }
}

.banner__wrapper::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 57px;
  background-image: url("../img/banner-line-mobile.svg");
  background-position: center;
}
@media (min-width: 768px) {
  .banner__wrapper::after {
    background-image: url("../img/banner-line-tablet.svg");
  }
}
@media (min-width: 1200px) {
  .banner__wrapper::after {
    background-image: url("../img/banner-line-desktop.svg");
  }
}

